'use client';

import { useState } from 'react';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { Textarea } from '@/components/ui/textarea';
import { BookkeepingService } from '@/lib/bookkeeping-utils';
import { Transaction, TransactionCategory } from '@/types/bookkeeping';

interface TransactionFormProps {
  transaction?: Transaction;
  onClose: () => void;
  onSave: () => void;
}

export default function TransactionForm({ transaction, onClose, onSave }: TransactionFormProps) {
  const [formData, setFormData] = useState<Partial<Transaction>>({
    id: transaction?.id || crypto.randomUUID(),
    date: transaction?.date || new Date().toISOString().split('T')[0],
    description: transaction?.description || '',
    debitAccount: transaction?.debitAccount || '',
    creditAccount: transaction?.creditAccount || '',
    amount: transaction?.amount || 0,
    category: transaction?.category || TransactionCategory.EXPENSE,
    reference: transaction?.reference || ''
  });

  const [errors, setErrors] = useState<Record<string, string>>({});

  const validateForm = (): boolean => {
    const newErrors: Record<string, string> = {};

    if (!formData.description?.trim()) {
      newErrors.description = 'Description is required';
    }
    if (!formData.debitAccount?.trim()) {
      newErrors.debitAccount = 'Debit account is required';
    }
    if (!formData.creditAccount?.trim()) {
      newErrors.creditAccount = 'Credit account is required';
    }
    if (!formData.amount || formData.amount <= 0) {
      newErrors.amount = 'Amount must be greater than 0';
    }
    if (formData.debitAccount === formData.creditAccount) {
      newErrors.creditAccount = 'Credit account must be different from debit account';
    }

    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    
    if (!validateForm()) return;

    const transactionData: Transaction = {
      id: formData.id!,
      date: formData.date!,
      description: formData.description!,
      debitAccount: formData.debitAccount!,
      creditAccount: formData.creditAccount!,
      amount: formData.amount!,
      category: formData.category!,
      reference: formData.reference
    };

    BookkeepingService.saveTransaction(transactionData);
    onSave();
  };

  const commonAccounts = [
    'Cash',
    'Bank Account',
    'Accounts Receivable',
    'Inventory',
    'Equipment',
    'Accounts Payable',
    'Owner\'s Capital',
    'Sales Revenue',
    'Cost of Goods Sold',
    'Rent Expense',
    'Utilities Expense',
    'Salary Expense',
    'Office Supplies',
    'Marketing Expense'
  ];

  return (
    <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center p-4 z-50">
      <Card className="w-full max-w-md max-h-[90vh] overflow-y-auto">
        <CardHeader>
          <CardTitle>{transaction ? 'Edit Transaction' : 'Add New Transaction'}</CardTitle>
        </CardHeader>
        <CardContent>
          <form onSubmit={handleSubmit} className="space-y-4">
            <div>
              <Label htmlFor="date">Date</Label>
              <Input
                id="date"
                type="date"
                value={formData.date}
                onChange={(e) => setFormData({ ...formData, date: e.target.value })}
                className="mt-1"
              />
            </div>

            <div>
              <Label htmlFor="description">Description *</Label>
              <Textarea
                id="description"
                value={formData.description}
                onChange={(e) => setFormData({ ...formData, description: e.target.value })}
                className={`mt-1 ${errors.description ? 'border-red-500' : ''}`}
                placeholder="Enter transaction description"
              />
              {errors.description && <p className="text-red-500 text-sm mt-1">{errors.description}</p>}
            </div>

            <div>
              <Label htmlFor="amount">Amount *</Label>
              <Input
                id="amount"
                type="number"
                step="0.01"
                min="0"
                value={formData.amount}
                onChange={(e) => setFormData({ ...formData, amount: parseFloat(e.target.value) || 0 })}
                className={`mt-1 ${errors.amount ? 'border-red-500' : ''}`}
                placeholder="0.00"
              />
              {errors.amount && <p className="text-red-500 text-sm mt-1">{errors.amount}</p>}
            </div>

            <div>
              <Label htmlFor="category">Category</Label>
              <Select
                value={formData.category}
                onValueChange={(value) => setFormData({ ...formData, category: value as TransactionCategory })}
              >
                <SelectTrigger className="mt-1">
                  <SelectValue placeholder="Select category" />
                </SelectTrigger>
                <SelectContent>
                  <SelectItem value={TransactionCategory.ASSET}>Asset</SelectItem>
                  <SelectItem value={TransactionCategory.LIABILITY}>Liability</SelectItem>
                  <SelectItem value={TransactionCategory.EQUITY}>Equity</SelectItem>
                  <SelectItem value={TransactionCategory.REVENUE}>Revenue</SelectItem>
                  <SelectItem value={TransactionCategory.EXPENSE}>Expense</SelectItem>
                  <SelectItem value={TransactionCategory.COST_OF_GOODS_SOLD}>Cost of Goods Sold</SelectItem>
                </SelectContent>
              </Select>
            </div>

            <div>
              <Label htmlFor="debitAccount">Debit Account *</Label>
              <Select
                value={formData.debitAccount}
                onValueChange={(value) => setFormData({ ...formData, debitAccount: value })}
              >
                <SelectTrigger className={`mt-1 ${errors.debitAccount ? 'border-red-500' : ''}`}>
                  <SelectValue placeholder="Select debit account" />
                </SelectTrigger>
                <SelectContent>
                  {commonAccounts.map((account) => (
                    <SelectItem key={account} value={account}>{account}</SelectItem>
                  ))}
                </SelectContent>
              </Select>
              {errors.debitAccount && <p className="text-red-500 text-sm mt-1">{errors.debitAccount}</p>}
            </div>

            <div>
              <Label htmlFor="creditAccount">Credit Account *</Label>
              <Select
                value={formData.creditAccount}
                onValueChange={(value) => setFormData({ ...formData, creditAccount: value })}
              >
                <SelectTrigger className={`mt-1 ${errors.creditAccount ? 'border-red-500' : ''}`}>
                  <SelectValue placeholder="Select credit account" />
                </SelectTrigger>
                <SelectContent>
                  {commonAccounts.map((account) => (
                    <SelectItem key={account} value={account}>{account}</SelectItem>
                  ))}
                </SelectContent>
              </Select>
              {errors.creditAccount && <p className="text-red-500 text-sm mt-1">{errors.creditAccount}</p>}
            </div>

            <div>
              <Label htmlFor="reference">Reference</Label>
              <Input
                id="reference"
                value={formData.reference}
                onChange={(e) => setFormData({ ...formData, reference: e.target.value })}
                className="mt-1"
                placeholder="Invoice number, check number, etc."
              />
            </div>

            <div className="flex gap-2 pt-4">
              <Button type="submit" className="flex-1">
                {transaction ? 'Update' : 'Save'} Transaction
              </Button>
              <Button type="button" variant="outline" onClick={onClose} className="flex-1">
                Cancel
              </Button>
            </div>
          </form>
        </CardContent>
      </Card>
    </div>
  );
}
